.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 190);
  border-radius: 10px;
  overflow: hidden;
}
.p-browser {
  height: 20px;
  background-color: rgb(243, 242, 190);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}
.p-circle {
  background-color: white;
  border-radius: 50%;
  height: 10px;
  width: 10px;
  margin: 4px;
}
.p-img {
  width: 100%;
  object-fit: fill;
  transition: all 10s ease;
}
.p:hover .p-img {
  transform: translateY(-100%);
}

@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 20vh;
  }
}
